<template>
  <div class="top">
    <el-button type="primary" @click="handleProjection"></el-button>
  </div>
  <div class="mapBox">
    <div class="map" id="map-1">
      <div class="title">EPSG:3857</div>
    </div>
    <div class="map" id="map-2">
      <div class="title">ESRI:53009</div>
    </div>
  </div>
</template>

<script setup>
  import "ol/ol.css";
  import { onMounted } from "vue";
  import { Map, View, Graticule } from "ol";
  import { Tile as TileLayer, Vector as VectorLayer } from "ol/layer";
  import { defaults, FullScreen } from "ol/control";
  import { XYZ, Vector as VectorSource } from "ol/source";
  import { GeoJSON } from "ol/format";
  import { ATTRIBUTIONS } from "@/constants";
  import { get, Projection } from "ol/proj";
  import { register } from "ol/proj/proj4";
  import proj4 from "proj4";
  import updateMapSize from "@/hooks/updateMapSize";

  let transformMap;
  //ESRIESRI:53009
  proj4.defs(
    "ESRI:53009",
    "+proj=moll +lon_0=0 +x_0=0 +y_0=0 +a=6371000 " +
      "+b=6371000 +units=m +no_defs"
  );
  register(proj4);
  const sphereMollweideProjection = new Projection({
    code: "ESRI:53009", //
    extent: [
      -9009954.605703328, -9009954.605703328, 9009954.605703328,
      9009954.605703328,
    ], //
    worldExtent: [-179, -90, 179, 90], //
  });

  const vectLayer = new VectorLayer({
    source: new VectorSource({
      url: "data/geojson/countries.geojson",
      format: new GeoJSON(),
      wrapX: false,
    }),
  });

  const initMap = () => {
    const map = new Map({
      //map
      target: "map-1",
      //
      layers: [
        new TileLayer({
          title: "",
          source: new XYZ({
            attributions: ATTRIBUTIONS,
            url: "http://t0.tianditu.com/DataServer?T=vec_w&x={x}&y={y}&l={z}&tk=3bc6874f2b919aa581635abab7759a3f",
            wrapX: false,
          }),
        }),
        vectLayer,
        new TileLayer({
          title: "",
          source: new XYZ({
            attributions: ATTRIBUTIONS,
            url: "http://t0.tianditu.com/DataServer?T=cva_w&x={x}&y={y}&l={z}&tk=3bc6874f2b919aa581635abab7759a3f",
            wrapX: false,
          }),
        }),
      ],
      view: new View({
        projection: get("EPSG:3857"), // EPSG:4326EPSG:3857
        center: [0, 0], // 
        //
        zoom: 1,
      }),
      //
      controls: defaults().extend([
        new FullScreen(), //IE
      ]),
    });
    // 
    updateMapSize(map);
  };
  // 
  const handleProjection = () => {
    if (transformMap == null || transformMap == undefined) {
      transformMap = new Map({
        layers: [
          new VectorLayer({
            source: new VectorSource({
              url: "/data/geojson/countries.geojson",
              format: new GeoJSON(),
            }),
          }),
        ],
        target: "map-2",
        view: new View({
          projection: sphereMollweideProjection, //
          center: [120, 40],
          zoom: 1,
        }),
      });
      new Graticule({
        //
        map: transformMap, //
      });
    }
  };
  onMounted(() => {
    initMap();
  });
</script>

