    position: relative;
    height: calc(100% - 50px);
    display: flex;
    justify-content: space-around;
  }
  .map {
    position: relative;
    width: 48%;
    height: 95%;
    border: 2px dashed red;
    box-shadow: 0 0 10px #000;
  }
  .title {
    width: 200px;
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
    color: red;
  }
  .mobile .mapBox {
    flex-direction: column;
    padding: 20px;
    box-sizing: border-box;
  }
  .mobile .map {
    width: 100%;
    height: 48%;
  }
</style>
